.feed{
    flex:5.5;
    height: calc(100vh - 50px);
    overflow-y: scroll;
}

::-webkit-scrollbar{
    width: 5px;
}

::-webkit-scrollbar-track{
    background-color: #f1f1f1;
}

::-webkit-scrollbar-thumb{
    background-color: rgb(179, 179, 179);
}


.feedWrapper{
    padding: 20px;
}