.post{
    width: 100%;
    border-radius: 10px;
    -webkit-box-shadow:0px 1px 3px 5px rgba(0,0,0,0.37);
    box-shadow:0px 1px 3px 5px rgba(0,0,0,0.37);
    margin: 30px 0;
}

.postWrapper{
    padding: 10px;
}

.postTop{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postTopLeft{
    display: flex;
    align-items: center;
}

.postProfileImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
}

.postUsername{
    font-size: 15px;
    font-weight: 500;
    margin:0 10px ;
}

.postDate{
    font-size: 17px;

}

.postCenter{
    margin: 20px 0;

}

.postImg{
    margin-top: 20px;
    width: 100%;
    max-height: 500px;
    object-fit: contain;
}

.postBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.postBottomLeft{
    display: flex;
    align-items: center;
}

.likeIcon{
height: 24px;
width: 24px;
margin-right: 5px;
cursor: pointer;
}

.postCounter{
    font-size: 15px;
}

.postCommentText{
    cursor: pointer;
    border-bottom: 1px dashed;
    font-size: 15px;
}